<template>
  <div class="contain_wrap">
    <div class="return" @click="returnGo">
      <van-icon name="arrow-left" />
    </div>
    <!-- 收货地址 -->
    <router-link to="/addresslist" class="revice_address_row">
      <div class="address_btn" v-if="!address.userName">
        <van-button type="primary" plain>收货地址</van-button>
      </div>
      <div class="address_btn" v-else>
        <div class="user_info">
          <div class="user_name">{{address.userName}}</div>
          <div class="user_tel">{{address.telNumber}}</div>
        </div>
        <div class="user_place">{{address.all+' '+address.detailed}}</div>
      </div>
    </router-link>
    <!-- 购物车内容 开始-->
    <div class="cart_wrapp">
      <div v-if="carts.length!==0">
        <div class="cart_item" v-for="item in carts" :key="item.goods_id">
          <!-- 复选框 -->
          <div class="cart_check_wrap">
            <van-checkbox-group v-model="goods">
              <van-checkbox checked-color="red" :name="item" @click="handleChage(item.goods_id)">
                <!-- 图片 -->
                <div class="cart_img_wrap">
                  <van-image
                    :src="item.pics[0].pics_sma_url||'https://ww1.sinaimg.cn/large/007rAy9hgy1g24by9t530j30i20i2glm.jpg'"
                  />
                  <!-- 商品信息 -->
                  <div class="cart_info_wrap">
                    <div class="user_name">{{item.goods_name}}</div>
                    <div class="price_num_wrap">
                      <div class="price">￥{{item.goods_price}}</div>
                      <div class="num_wrap">
                        <button
                          @click.stop="handleChangeNum(item.goods_id,-1)"
                          class="num_calculate"
                        >-</button>
                        <div class="num">{{item.num}}</div>
                        <button
                          @click.stop="handleChangeNum(item.goods_id,+1)"
                          class="num_calculate"
                        >+</button>
                      </div>
                    </div>
                  </div>
                </div>
              </van-checkbox>
            </van-checkbox-group>
          </div>
        </div>
      </div>
      <div v-else class="no_wrap">
        <van-image
          src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3214428190,3968304704&fm=26&gp=0.jpg"
          mode="widthFix"
        />
        <text>购物车空空如也</text>
      </div>
    </div>
    <!-- 购物车内容 结束-->

    <!-- 工具栏 开始-->
    <div class="tool_wrap">
      <!-- 全选 -->
      <div class="all_check_wrap">
        <van-checkbox checked-color="red" @click="handleAllChage" v-model="allcheck">
          <span>全选</span>
        </van-checkbox>
      </div>
      <!-- 合计 -->
      <div class="all_price_wrap">
        <div class="prive">
          <span>合计:</span>
          <span class="font_color">￥{{totalPrice}}</span>
        </div>
        <div class="freight_text">包含运费</div>
      </div>
      <!-- 结算 -->
      <div class="view set_wrap" @click="handlrTotalSet">结算({{totalNum}})</div>
    </div>
    <FootTab></FootTab>
  </div>
</template>

<script>
import { reaxios } from "@/assets/js/reaxios";
import "@/assets/css/category.css";
import FootTab from "../components/FootTab";
export default {
  data() {
    return {
      // 用户信息
      address: {},

      // 购物车信息
      carts: [],

      // 勾选数据
      goods: [],

      // 全选
      allcheck: true,

      // 总价
      totalPrice: 0,

      // 总数量
      totalNum: 0
    };
  },

  created() {
    this.carts = JSON.parse(localStorage.getItem("cart")) || [];

    this.handleAllChage();
    this.setCart(this.goods);
    this.getAddress();
  },
  components: {
    FootTab
  },

  methods: {
    setCart(goods) {
      // const goods = JSON.parse(localStorage.getItem("goods"));
      let totalPrice = 0;
      let totalNum = 0;
      goods.forEach(v => {
        totalPrice += v.num * v.goods_price;
        totalNum += v.num;
      });
      this.totalPrice = totalPrice;
      this.totalNum = totalNum;
    },
    // 获取地址
    getAddress() {
      const id =
        JSON.parse(localStorage.getItem("address_id")) ||
        this.$route.query.address_id;
      const addressArray = JSON.parse(localStorage.getItem("addressArray"));
      this.address = addressArray[id] || {};
      localStorage.setItem("address_id", id);
    },
    // 商品选择
    handleChage(id) {
      this.allcheck = this.carts.length == this.goods.length;
      this.setCart(this.goods);
      localStorage.setItem("goods", JSON.stringify(this.goods));
    },

    // 全选-反选
    handleAllChage() {
      this.allcheck = this.carts.length ? this.allcheck : false;
      if (this.allcheck) {
        this.carts.forEach(v => {
          this.goods.push(v);
        });
      } else {
        this.goods.splice(0, this.goods.length);
      }
      localStorage.setItem("goods", JSON.stringify(this.goods));
      this.setCart(this.goods);
    },

    // 修改商品数量
    handleChangeNum(id, opration) {
      // 获取修改数量的索引值
      const index = this.carts.findIndex(v => v.goods_id == id);
      if (this.carts[index].num === 1 && opration === -1) {
        // 是否删除元素
        this.$dialog({
          title: "提示",
          message: "是否要删除商品"
        }).then(res => {
          this.carts.splice(index, 1);
          localStorage.setItem("cart", JSON.stringify(this.carts));
        });
      } else {
        this.carts[index].num += opration;
        this.goods[index].num = this.carts[index].num;
        localStorage.setItem("cart", JSON.stringify(this.carts));
      }

      this.goods[index].num = this.carts[index].num;
      this.setCart(this.goods);
      localStorage.setItem("goods", JSON.stringify(this.goods));
    },

    returnGo() {
      this.$router.go(-1);
    },

    // 结算
    handlrTotalSet() {
      // 判断地址，商品是否为空
      if (!this.address.userName) {
        this.$toast({
          message: "收货地址不能为空"
        });
        return;
      }
      if (this.totalNum === 0) {
        this.$toast({
          message: "购物车不能为空"
        });
        return;
      }
      this.$toast({
        message: "支付成功"
      });
      this.$router.push("/user");
    }
  }
};
</script>

<style lang="less" scoped>
.contain_wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(243, 243, 243);
  .return {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 95%;
    background-color: var(--themeColor);
    text-align: left;
    font-size: 24px;
    display: flex;
    align-items: center;
    color: aliceblue;
  }

  .revice_address_row {
    background-color: #fff;
    box-shadow: 0 0 5px var(--grayColor);
    margin: 50px 5px 10px;
    display: block;
    color: #000;
    .address_btn {
      padding: 10px;
      button {
        width: 60%;
      }
    }

    .user_info {
      display: flex;
      justify-content: space-between;
      text-align: left;
      font-size: 16px;
      .user_name {
        flex: 1;
        padding-bottom: 5px;
      }
      .user_tel {
        flex: 1;
        text-align: right;
      }
    }
    .user_place {
      text-align: left;
      display: -webkit-box;
      overflow: hidden;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
    }
  }

  .cart_wrapp {
    margin: 0 5px;
    border-top: 2px solid var(--grayColor);
    .cart_item {
      display: flex;
      justify-content: center;
      align-items: center;
      border-bottom: 2px solid var(--grayColor);
      background-color: #fff;
      padding: 10px;

      //复选框
      .cart_check_wrap {
        width: 100%;
      }

      // 图片
      .cart_img_wrap {
        display: flex;
        flex: 2;
        .van-image {
          width: 20%;
          margin: 0 10px;
        }
        // 商品信息
        .cart_info_wrap {
          flex: 5;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          text-align: left;
          .user_name {
            display: -webkit-box;
            overflow: hidden;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            color: #000;
          }
          .price_num_wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .price {
              font-size: 18px;
              color: var(--themeColor);
            }
            .num_wrap {
              display: flex;
              .num_calculate {
                border: 1px solid rgb(219, 219, 219);
                height: 28px;
                width: 28px;
                display: flex;
                justify-content: center;
                align-items: center;
                color: #000;
                background-color: rgba(0, 0, 0, 0);
              }
              .num {
                height: 28px;
                width: 28px;
                text-align: center;
                line-height: 28px;
              }
            }
          }
        }
      }
    }

    .no_wrap {
      width: 150px;
      margin: 100px auto;
      text-align: center;
      color: #ccc;
    }
  }
  // 工具栏
  .tool_wrap {
    display: flex;
    position: fixed;
    bottom: 50px;
    width: 100%;
    border-top: 1px solid var(--themeColor);
    background-color: #fff;

    .all_check_wrap {
      flex: 2;
      padding: 5px;
      display: flex;
      align-items: center;
      font-size: 18px;
    }
    .all_price_wrap {
      text-align: right;
      padding: 5px;
      flex: 3;
      .prive {
        font-size: 18px;
        .font_color {
          color: red;
        }
      }
      .freight_text {
        font-size: 12px;
        color: #ccc;
      }
    }
    .set_wrap {
      flex: 2;
      font-size: 18px;
      color: #fff;
      width: 100px;
      background-color: red;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
</style>


