<template>
  <div class="foottab">
    <div class="foottab_item" v-for="(item,index) in tabList" :key="index" @click="selected">
      <router-link class="navigation" :to="item.pagePath">
        <i :class="['fa',item.icon,{'active':item.isActive}]" aria-hidden="true"></i>
        <div :class="['foottab_title',{'active':item.isActive}]">{{item.text}}</div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tabList: [
        {
          pagePath: "/",
          text: "首页",
          icon: "fa-home",
          isActive: true
        },
        {
          pagePath: "/category",
          text: "分类",
          icon: "fa-th-large",
          isActive: false
        },
        {
          pagePath: "/cart",
          text: "购物车",
          icon: "fa-shopping-cart",
          isActive: false
        },
        {
          pagePath: "/user",
          text: "我的",
          icon: "fa-user",
          isActive: false
        }
      ]
    };
  },
  created() {
    this.selected();
  },
  methods: {
    selected() {
      let fullPath = this.$route.fullPath;
      this.tabList.forEach((v, i) => {
        v.isActive = false;
        if (fullPath == v.pagePath) {
          v.isActive = true;
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.foottab {
  position: fixed;
  bottom: 0;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 50px;
  .navigation {
    color: var(--secondColor);
    .fa {
      font-size: 24px;
    }
    .active {
      color: var(--themeColor);
    }
  }
}
</style>